body {
  background-color: #f4f4f9;
  font-family: 'Arial', sans-serif;
}

.AppContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  width: 100%;
}

.chart-and-adoft {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 20px;
}





.animal-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  overflow: visible; /* 스크롤이 아닌 자연스럽게 표시 */
}

.animal-card {
  flex: 1 1 21%;
  margin: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.animal-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.view-all {
  margin-top: 20px;
}

.view-all a {
  text-decoration: none;
  color: #007bff;
  font-weight: bold;
}

@media (max-width: 768px) {
  .chart-and-adoft {
      flex-direction: column;
  }

  .chart-container, .adoft-container {
      width: 100%; /* 모바일에서 전체 너비 */
      height: auto; /* 높이 자동 조정 */
  }

  .animal-card {
      flex: 1 1 100%;
  }
}
