.adoft {
    text-align: center;
    padding: 20px;

}

.animal-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px; /* 카드 사이의 간격 설정 */
}

.animal-card {
    flex: 1 1 calc(20% - 20px);
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px; /* 아래쪽 간격 추가 */
    text-align: left;
    box-sizing: border-box; /* 패딩과 보더를 포함한 너비 계산 */
}

.animal-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
}



